// src/main.js
import { createApp } from 'vue';
import App from './App.vue';
import ElementPlus from 'element-plus'; // Import Element Plus
import 'element-plus/theme-chalk/index.css'; // Import CSS mặc định của Element Plus
import 'bootstrap/dist/css/bootstrap.css'; // Import Bootstrap CSS
import './assets/css/custom.css'; // Import file CSS tùy chỉnh
import router from './router'; // Import router
import i18n from './i18n';
import { createPinia } from 'pinia';
import 'bootstrap';

const pinia = createPinia();
const app = createApp(App);
app.use(ElementPlus);
app.use(router); // Sử dụng router
app.use(i18n);
app.use(pinia);
app.mount('#app');
