import { createI18n } from 'vue-i18n';

// Tải tất cả các tệp ngôn ngữ từ thư mục locales
const messages = {};

const locales = require.context('./locales', true, /\.json$/);
locales.keys().forEach((key) => {
  const locale = key.match(/\.\/(.*)\.json/)[1]; // Lấy tên locale từ tệp
  messages[locale] = locales(key); // Thêm vào đối tượng messages
});

// Cấu hình vue-i18n
const i18n = createI18n({
  locale: 'en', // Ngôn ngữ mặc định
  messages,
});

export default i18n;
